import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Row from 'react-bootstrap/Row';
import { Bar } from '@vx/shape';
import { Group } from '@vx/group';
import { scaleBand, scaleLinear } from '@vx/scale';


export default class Sorting extends Component {
    constructor() {
        super();
        this.state = {
            size: 100, interval: 3,
            method: "BUBBLE",
            data: this.generateArray(100),
            intervalId: null,
            queue: [],
        }
        this.handleClickSort = this.handleClickSort.bind(this);
        this.handleClickReset = this.handleClickReset.bind(this);
        this.handleClickMethod = this.handleClickMethod.bind(this);

        this.bubbleSort = this.bubbleSort.bind(this);
        this.insertionSort = this.insertionSort.bind(this);
        this.selectionSort = this.selectionSort.bind(this);
        this.mergeSort = this.mergeSort.bind(this);
        this.merge = this.merge.bind(this);
        this.quickSort = this.quickSort.bind(this);
        this.partition = this.partition.bind(this);
        this.heapSort = this.heapSort.bind(this);
        this.heapify = this.heapify.bind(this);
        this.cocktailSort = this.cocktailSort.bind(this);

        this.startAnimation = this.startAnimation.bind(this);
        this.dequeue = this.dequeue.bind(this);
    }

    generateArray(n) {
        var arr = [];
        for (var i = 0; i < n; i ++) { arr[i] = { index: i, score: i + 1 } }
        var temp, curr, top = arr.length;
        if (top) while(--top) {
            curr = Math.floor(Math.random() * (top + 1));
            temp = arr[curr];
            arr[curr] = arr[top];
            arr[top] = temp;
        }
        return arr;
    }

    handleInputChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        });
    }
    
    handleClickMethod(method) {
        this.setState({ method });
        this.handleClickReset();
    }

    handleClickReset() {
        this.stopInterval();
        const { size } = this.state;
        const data = this.generateArray(size);
        const queue = [];
        this.setState({ data, queue });
    }

    handleClickSort() {
        const { data, method } = this.state;
        switch(method) {
            case "BUBBLE":
                this.bubbleSort([...data]);
                break;
            case "COCKTAIL":
                this.cocktailSort([...data]);
                break;
            case "INSERTION":
                this.insertionSort([...data]);
                break;
            case "SELECTION":
                this.selectionSort([...data]);
                break;
            case "MERGE":
                this.mergeSort([...data], 0, data.length - 1);
                break;
            case "QUICK":
                this.quickSort([...data], 0, data.length - 1);
                break;
            case "HEAP":
                this.heapSort([...data]);
                break;
            default:
                break;
        }
        this.startAnimation();
    }

    stopInterval() {
        clearInterval(this.state.intervalId);
    }

    bubbleSort(data) {
        const queue = [];
        for (var i = 0; i < data.length; i ++) {
            for (var j = 0; j < data.length - i - 1; j ++) {
                if (data[j].score > data[j+1].score) {
                    [data[j], data[j+1]] = [data[j+1], data[j]];
                    queue.push([...data]);
                }
            }
        }
        this.setState({ queue });
    }

    insertionSort(data) {
        const queue = [];
        for (var i = 0 + 1; i < data.length; i ++) {
            const key = data[i];
            var j = i - 1;
            while (j >= 0 && key.score < data[j].score) {
                data[j+1] = data[j];
                j -= 1;
            }
            data[j+1] = key;
            queue.push([...data]);
        }
        this.setState({ queue });
    }

    selectionSort(data) {
        const queue = [];
        for (var i = 0; i < data.length; i ++) {
            var minIndex = i;
            for (var j = i + 1; j < data.length; j ++) {
                if (data[minIndex].score > data[j].score) {
                    minIndex = j;
                }
            }

            [data[i], data[minIndex]] = [data[minIndex], data[i]]
            queue.push([...data]);
        }
        this.setState({ queue });
    }

    async mergeSort(data, start, end) {
        if (start < end) {
            const mid = start + Math.floor((end - start) / 2);
            await Promise.all([
                this.mergeSort(data, start, mid),
                this.mergeSort(data, mid + 1, end),
            ]);

            this.merge(data, start, mid, end);
        }
    }

    merge(data, start1, mid, end) {
        const { queue } = this.state;
        let start2 = mid + 1;

        if (data[mid].score < data[start2].score) {
            return;
        }

        while (start1 <= mid && start2 <= end) {
            if (data[start1].score < data[start2].score) {
                start1 ++;
            } else {
                const key = data[start2];
                let minIndex = start2;
                while (minIndex !== start1) {
                    data[minIndex] = data[minIndex-1];
                    minIndex --;
                }
                data[start1] = key;
                queue.push([...data]);

                start1 ++;
                mid ++;
                start2 ++;
            }
        }
        this.setState({ queue });
    }

    quickSort(data, start, end) {
        if (start < end) {
            const partitionIndex = this.partition(data, start, end);
            this.quickSort(data, start, partitionIndex - 1);
            this.quickSort(data, partitionIndex + 1, end);
        }
    }

    partition(data, start, end) {
        const { queue } = this.state;
        const pivot = start++;

        while (true) {
            while (start <= end && data[end].score >= data[pivot].score) {
                end --;
            }

            while (start <= end && data[start].score <= data[pivot].score) {
                start ++;
            }

            if (start <= end) {
                [data[start], data[end]] = [data[end], data[start]]
                queue.push([...data]);
            } else {
                break;
            }
        }
        [data[pivot], data[end]] = [data[end], data[pivot]]
        queue.push([...data]);
        this.setState({ queue });
        return end;
    }

    heapSort(data) {
        const { queue } = this.state;
        for (var i = data.length - 1; i >= 0; i --) {
            this.heapify(data, data.length - 1, i);
        }

        for (i = data.length - 1; i > 0; i --) {
            [data[i], data[0]] = [data[0], data[i]];
            queue.push([...data]);
            this.heapify(data, i, 0);
        }
        this.setState({ queue });
    }

    heapify(data, n, i) {
        const { queue } = this.state;
        var largest = i, left = 2 * i + 1, right = 2 * i + 2;

        if (left < n && data[i].score < data[left].score) {
            largest = left; 
        }

        if (right < n && data[largest].score < data[right].score) {
            largest = right;
        }

        if (largest !== i) {
            [data[i], data[largest]] = [data[largest], data[i]];
            queue.push([...data]);
            this.heapify(data, n, largest);
        }
        this.setState({ queue });
    }

    cocktailSort(data) {
        const queue = [];
        var swapped = true, start = 0, end = data.length - 1, i = 0;
        while (swapped) {
            swapped = false;

            for (i = start; i < end; i ++) {
                if (data[i].score > data[i+1].score) {
                    [data[i], data[i+1]] = [data[i+1], data[i]];
                    queue.push([...data]);
                    swapped = true;
                }
            }

            if (!swapped) { break; }

            swapped = false;

            end --;

            for (i = end - 1; i > start - 1; i --) {
                if (data[i].score > data[i+1].score) {
                    [data[i], data[i+1]] = [data[i+1], data[i]];
                    queue.push([...data]);
                    swapped = true;
                }
            }

            start ++;
        }
        this.setState({ queue });
    }

    startAnimation() {
        const { interval } = this.state;
        const intervalId = setInterval(() => {
            this.dequeue();
        }, 1000 / Math.pow(10, interval));

        this.setState({ intervalId })
    }

    dequeue() {
        const { queue } = this.state;
        if (queue.length) {
            const data = queue.shift();
            this.setState({ data });
            return;
        }
        this.stopInterval();
    }

    render() { 
        const { width, height } = this.props;
        const { data, size, interval } = this.state;

        // accessors
        const x = d => d.index;
        const y = d => d.score;

        // bounds
        const xMax = width;
        const yMax = height - 100;

        // scales
        const xScale = scaleBand({
            rangeRound: [0, xMax],
            domain: data.map(x),
            padding: 0.4
        });
        const yScale = scaleLinear({
            rangeRound: [yMax, 0],
            domain: [0, Math.max(...data.map(y))]
        });

        return (
            <Card>
                <Card.Header>
                    <h3>Sorting</h3>
                </Card.Header>
                <Card.Body style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <svg width={width} height={height}>
                        <rect width={width} height={height} fill={"#242424"} rx={14} />
                        <Group top={50}>
                            {data.map((d, i) => {
                                const letter = x(d);
                                const barWidth = xScale.bandwidth();
                                const barHeight = yMax - yScale(y(d));
                                const barX = xScale(letter);
                                const barY = yMax - barHeight;
                                return (
                                    <Bar
                                        key={`bar-${letter}`}
                                        x={barX}
                                        y={barY}
                                        width={barWidth}
                                        height={barHeight}
                                        fill="rgba(23, 233, 217, .5)"
                                    />
                                );
                            })}
                        </Group>
                    </svg>
                </Card.Body>
                <Card.Footer>
                    <Nav fill variant='tabs' defaultActiveKey='bubble'>
                        <Nav.Item>
                            <Nav.Link eventKey="bubble" onClick={() => this.handleClickMethod('BUBBLE')}>Bubble</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="insertion" onClick={() => this.handleClickMethod('INSERTION')}>Insertion</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="selection" onClick={() => this.handleClickMethod('SELECTION')}>Selection</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="merge" onClick={() => this.handleClickMethod('MERGE')}>Merge</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="quick" onClick={() => this.handleClickMethod('QUICK')}>Quick</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="heap" onClick={() => this.handleClickMethod('HEAP')}>Heap</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="cocktail" onClick={() => this.handleClickMethod('COCKTAIL')}>Cocktail</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    <br />
                    <Row>
                        <Form.Group as={Col}>
                            <Form.Label>Size: { size }</Form.Label>
                            <Form.Control name="size" type="range" min={10} max={500} step={10} value={size} onChange={this.handleInputChange} />
                        </Form.Group>
                        <Form.Group as={Col}>
                            <Form.Label>Speed: { parseInt(interval) + 1 }</Form.Label>
                            <Form.Control name="interval" type="range" min={0} max={3} step={1} value={interval} onChange={this.handleInputChange} />
                        </Form.Group>
                        <Col>
                            <Button variant="dark" onClick={this.handleClickReset} block>Generate</Button>
                            <Button variant="dark" onClick={this.handleClickSort} block>Sort</Button>
                        </Col>
                    </Row>
                </Card.Footer>
            </Card>
        );
    }
}